import React, { Component } from "react";
import {
  Modal,
  List,
  Button,
  DatePicker,
  TextareaItem,
  InputItem
} from "antd-mobile";
import { createForm } from "rc-form";

import enUs from "antd-mobile/lib/date-picker/locale/en_US";

export class InputModal extends Component {
  state = {
    date: null
  };
  render() {
    const { getFieldProps } = this.props.form;
    return (
      <>
        <Modal
          popup
          visible={this.props.visible}
          onClose={this.props.onClose("modal2")}
          animationType="slide-up"
          afterClose={() => {
            // alert("afterClose");
          }}
        >
          <List
            renderHeader={() => <div>근로시간 등록</div>}
            //className="popup-list"
          >
            <DatePicker
              mode="date"
              locale={enUs}
              title="Select Date"
              extra="Optional"
              value={this.state.date}
              onChange={date => this.setState({ date })}
            >
              <List.Item arrow="horizontal">일자</List.Item>
            </DatePicker>
            {/* <DatePicker
              mode="time"
              locale={enUs}
              minuteStep={2}
              use12Hours
              value={this.state.time}
              onChange={time => this.setState({ time })}
            >
              <List.Item arrow="horizontal">근무시간</List.Item>
            </DatePicker> */}
            <InputItem
              {...getFieldProps("number")}
              type="number"
              placeholder="click to show number keyboard"
              title="근무시간"
              labelNumber={5}
            >
              근무시간
            </InputItem>
            <InputItem
              {...getFieldProps("digit")}
              type="digit"
              placeholder="click to show native number keyboard"
            >
              근무시간
            </InputItem>
            <TextareaItem
              title="사유"
              placeholder="click the button below to focus"
              data-seed="logId"
              autoHeight
              rows={3}
              ref={el => (this.customFocusInst = el)}
            />

            <List.Item>
              <Button type="primary" onClick={this.props.onClose("modal2")}>
                닫기
              </Button>
            </List.Item>
          </List>
        </Modal>
      </>
    );
  }
}

const InputModalWrapper = createForm()(InputModal);
export default InputModalWrapper;
